.innovations {
    top: 0;
    left: 0;
    padding-top: 80px;    
    &__container {
        display: flex;
        justify-content: space-between;
    }
    &__left {
        background-color: var(--color-accent-1);
        color: var(--color-white);
        max-width: 42%;
    }
    &__title {
        font-weight: 400;
        display: inline-block;
        font-size: 18px;
        background-color: var(--color-accent-2);
        padding: 10px;
        margin: 40px 0 30px 40px;
        color: var(--color-white);
    }
    &__descr {
        font-size: 30px;
        line-height: 1.5;
        margin: 0 0 30px 40px;
    }
    &__arrow {
        margin-right: 20px;
    }
    &__more {
        margin: 0 0 30px 40px;
    }
    &__img {
        background-image: url("../img/hero.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 280px;
    }
    &__right {
        width: 58%;
        position: relative;
    }
    &__title--right {
        margin: 40px 0 30px 135px;
    }
}
.statistic {
    &__item {
        z-index: 999;
        display: flex;
        flex-direction: row;
        color: gray;
        margin: 0 0 55px 135px;
    }
    &__item:hover {
        transform: scale(1.8);
        transition: ease-in-out 0.3s;
        color: var(--color-accent-2);
        margin-left: 300px;
    }
    &__number {
        font-size: 48px;
        margin: 0;
        padding-right: 10px;
    }
    &__descr {
        max-width: 170px;
        line-height: 1.5;
        margin: 0;
        font-size: 14px;
        padding-top: 5px;
    }
}
.patent {
        position: absolute;
        background-image: url("../img/patent.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 306px;
        height: 400px;
        top: 100px;
        right: 50px;
}