.nav {
    padding:  35px 0;
    
    &__container {
        background-color: var(--color-accent-2);
    }
    &--flex {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__item {
        color: var(--color-white);
        font-size: 18px;
        padding-right: 65px;
    }
    &__link {
        display: flex;
        align-items: center;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 175px;
        height: 40px;
        background-color: var(--color-white);
        color: var(--color-black);
        font-size: 18px;
    }
}