.header {
    left: 0;
    top: 0;
    color: var(--color-black);
    font: var(--medium-regular);
    font-size: 16px;
    background-color: var(--color-white);
    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        
    }
    
    &__item {
        display: flex;
        align-items: center;
        padding: 0 28px;
        height: 90px;
    }
    &__border:not(:last-child) {
        border-right: 1px solid #C4C4C4;
    }
}
.salon {
    display: flex;
    align-items: center;
    padding: 0 30px;
    height: 90px;
    &__link {
        display: flex;
        align-items: center;
        flex-direction: row;
        text-decoration: none;
        color: var(--color-black);
    }
    &__text {
        padding-right: 15px;
    }
}
.blind {
    display: flex;
    align-items: center;
    padding: 0 30px;
    height: 90px;
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 270px;
        height: 40px;
        background-color: var(--color-accent-1);
        color: var(--color-white);
        text-transform: uppercase;
        font-size: 14px;
    }
    &__text {
        padding-left: 10px;
    }
}
.tel {
    display: flex;
    align-items: center;
    padding: 0 30px;
    height: 90px;
    &__number {
    color: var(--color-black);
    
    }
}
.logo {
    display: flex;
    align-items: center;
    height: 90px;
    padding: 0 30px;
    &__logo {
        height: 55px;
        width: 190px;
    }
}
